import { useState } from "react";
import "./styles.css";

export default function App({ data }) {
  const [expand, setExpand] = useState(false);

  console.log(data);
  return (
    <div className="App">
      <p onClick={() => setExpand(!expand)}>{data.name}</p>
      {expand && (
        <div style={{ paddingLeft: 15 }}>
          {data.items.map((item) => {
            return <App data={item} />;
          })}
        </div>
      )}
    </div>
  );
}
